// @flow
import React from 'react';
import { connect } from 'react-redux';

import { updateSensitivity } from '../../actions/model';

import * as API from '../../model/api';

import { H5, Card } from '@blueprintjs/core';

import { SubmitButton } from '../../components/Form/Form';

import { type Deliverable } from '../../model/api/types';

import { type State } from '../../reducers/types';
import { type Dispatch } from '../../actions/types';

// TODO: Extract to own component
import { SwitchInput } from './BenefitSensitivity';

type CurrentValues = Array<{ id: string, switch: boolean }>;

type DeliverableSensitivityProps = {
  scenarioId: string,
  deliverables: Array<Deliverable>,
  deliverableSensitivity: CurrentValues,
  handleSave: (values: CurrentValues) => void,
  width: string | number,
};

type DeliverableSensitivityItemProps = {
  deliverable: Deliverable,
  switchState: boolean,
  handleSwitchChange: () => void,
  width: string | number,
};

const handleSwitchChange = (
  targetId: string,
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void
) => () => {
  const filteredValues = currentValues.filter(sensitivity => sensitivity.id !== targetId);
  const foundValue = currentValues.find(sensitivity => sensitivity.id === targetId);

  if (!foundValue) {
    throw new Error(`Could not find sensitivity value for deliverable id ${targetId}`);
  }

  const newValues = [...filteredValues, { ...foundValue, switch: !foundValue.switch }];
  setCurrentValues(newValues);
};

const DeliverableSensitivityItem = ({
  deliverable,
  switchState,
  handleSwitchChange,
  width,
}: DeliverableSensitivityItemProps) => {
  const title = deliverable.name;
  return (
    <Card style={{ marginBottom: '10px', marginRight: '10px', width }}>
      <H5>{title}</H5>
      <SwitchInput
        label="Switch"
        labelInfo="(required)"
        handleChange={handleSwitchChange}
        value={switchState}
      />
    </Card>
  );
};

const renderDeliverableSensitivityItem = (
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void,
  width: string | number
) => deliverable => {
  const foundSensitivity = currentValues.find(sensitivity => sensitivity.id === deliverable.id);
  if (!foundSensitivity) {
    throw new Error(`Could not find deliverable sensitivity with id ${deliverable.id}`);
  }

  const { switch: switchState } = foundSensitivity;

  const handleSwitchCallback = handleSwitchChange(deliverable.id, currentValues, setCurrentValues);

  return (
    <DeliverableSensitivityItem
      key={`DELIVERABLE_SENSITIVITY_ITEM_${deliverable.id}`}
      deliverable={deliverable}
      switchState={switchState}
      handleSwitchChange={handleSwitchCallback}
      width={width}
    />
  );
};

const DeliverableSensitivity = ({
  deliverables,
  deliverableSensitivity,
  handleSave,
  width,
}: DeliverableSensitivityProps) => {
  const [currentValues, setCurrentValues] = React.useState(deliverableSensitivity);

  return (
    <div style={{ display: 'flex', flexWrap: 'wrap' }}>
      {deliverables.map(renderDeliverableSensitivityItem(currentValues, setCurrentValues, width))}
      <SubmitButton onClick={() => handleSave(currentValues)} />
    </div>
  );
};

const mapStateToProps = (state: State, ownProps: { scenarioId: string }) => ({
  deliverables: API.getDeliverables(state.model),
  deliverableSensitivity: API.getScenarioById(state.model, ownProps.scenarioId)
    .deliverableSensitivity,
});

const mapDispatchToProps = (dispatch: Dispatch, ownProps: { scenarioId: string }) => ({
  handleSave: values =>
    dispatch(updateSensitivity(ownProps.scenarioId, 'deliverableSensitivity', values)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DeliverableSensitivity);
