// @flow
import React from 'react';

// Flow Types
import { type FormState, type Validate, type Warn, type Update } from './types';
import { type Model } from '../../model/api/types';

// Custom react hook
function useFormValidation(
  initialState: FormState,
  validate: Validate,
  warn: Warn,
  model: Model,
  update: Update
) {
  // Store current values of form in state
  const [values, setValues] = React.useState(initialState);

  // Store current errors and warnings in state
  const [errors, setErrors] = React.useState({});
  const [warnings, setWarnings] = React.useState({});

  // Keep track of whether form is submitting
  const [isSubmitting, setSubmitting] = React.useState(false);

  // Keep track of whether form has been touched or not
  const [touched, setTouched] = React.useState(false);

  // Side effect, dependent on errors, submtting, and values
  React.useEffect(() => {
    if (isSubmitting) {
      const noErrors = Object.keys(errors).length === 0;
      if (noErrors) {
        update(values);
        setSubmitting(false);
        setTouched(false);
      } else {
        setSubmitting(false);
      }
    }
  }, [errors, isSubmitting, update, values]);

  // Side effect for updated form values on different ids
  React.useEffect(() => {
    setValues(initialState);
  }, [initialState]);

  // Handles the change of an input value
  function handleChange(event: SyntheticInputEvent<HTMLInputElement>) {
    setValues({
      ...values,
      [event.target.name]: event.target.value,
    });

    setTouched(true);
  }

  // Validates when user blurs the field
  function handleBlur() {
    const validationErrors = validate(values, model);
    setErrors(validationErrors);

    const validationWarnings = warn(values, model);
    setWarnings(validationWarnings);

    setTouched(true);
  }

  // Function for handling the submit of data
  function handleSubmit(event: SyntheticInputEvent<HTMLInputElement>) {
    event.preventDefault();

    const validationErrors = validate(values, model);
    setErrors(validationErrors);
    const validationWarnings = warn(values, model);
    setWarnings(validationWarnings);

    setSubmitting(true);
  }

  // Return hook outputs as object
  return {
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    errors,
    warnings,
    isSubmitting,
    touched,
  };
}

export default useFormValidation;
