// @flow
import * as CONSTANTS from '../../../config/constants';
import type { Breadcrumbs } from '../types';
import type { State } from '../../../reducers/types';

/**
 * Creates breadcrumbs for the certainty analysis screen state.
 * @param  {State} state state tree
 * @return {Breadcrumbs}        breadcrumbs
 */
export function getBreadcrumbsData(state: State): Breadcrumbs {
  // Extract the selected element
  const { selected } = state.certainty.explorer;
  const { model } = state;
  const programme = model.programmes[0];
  const { scenarios } = state.model.programmes[0];

  const modelBreadcrumb = {
    icon: CONSTANTS.MODEL_ICON,
    text: model.name,
  };

  const programmeBreadcrumb = {
    icon: CONSTANTS.MODEL_ICON,
    text: programme.name,
  };

  const scenariosBreadcrumb = {
    icon: CONSTANTS.SCENARIO_ICON,
    text: 'Scenarios',
  };

  if (selected) {
    if (selected.type.id === CONSTANTS.SCENARIO_ID) {
      const scenarioBreadcrumb = {
        icon: selected.type.icon,
        text: selected.label,
      };
      return [modelBreadcrumb, programmeBreadcrumb, scenariosBreadcrumb, scenarioBreadcrumb];
    } else {
      // Try to find parent scenario
      const found = scenarios.find(s => selected.scenarioId === s.id);

      // If no scenario found then return error
      if (!found) {
        throw new Error('Could not find parent scenario when constructing breadcrumbs');
      }

      const scenarioBreadcrumb = {
        icon: CONSTANTS.SCENARIO_ICON,
        text: found.name,
      };
      const childBreadcrumb = {
        icon: selected.type.icon,
        text: selected.label,
      };
      return [
        modelBreadcrumb,
        programmeBreadcrumb,
        scenariosBreadcrumb,
        scenarioBreadcrumb,
        childBreadcrumb,
      ];
    }
  }

  // If nothing selected then render no breadcrumbs
  return [];
}
