// @flow
import React, { useState } from 'react';
import type { Node } from 'react';

import { Button, Intent } from '@blueprintjs/core';
import { Column, Table, EditableCell, RowHeaderCell } from '@blueprintjs/table';

// Flow Types
type Values = { [string]: { [string]: number } };

type Axis = Array<{ id: string, name: string }>;

type UpdateValues = (values: Values) => void;

type HandleSubmit = (typeKey: string, values: Values) => void;

export type MatrixProps = {
  x: Axis,
  y: Axis,
  handleSubmit: HandleSubmit,
  updateValues: UpdateValues,
  typeKey: string,
  values: Values,
};

// Returns data item from values prop
const returnData = (values: Values, rowId: string, columnId: string): number => {
  return values[rowId][columnId];
};

// Retreives ids from index
const idsFromIndex = (x: Axis, y: Axis, rowIndex: number, columnIndex: number) => {
  return [x[rowIndex].id, y[columnIndex].id];
};

// Correctly calls redux store function updateValues
const handleConfirm = (
  newValue,
  oldValues,
  x,
  y,
  rowIndex,
  columnIndex,
  updateValues,
  setChanged
) => {
  const ids = idsFromIndex(x, y, rowIndex, columnIndex);
  if (Number.parseFloat(newValue) !== oldValues[ids[0]][ids[1]]) {
    setChanged(true);
  }
  const values = {
    ...oldValues,
    [ids[0]]: {
      ...oldValues[ids[0]],
      [ids[1]]: Number.parseFloat(newValue),
    },
  };
  updateValues(values);
};

// Renders a cell with values from prop
const renderCell = (values: Values, x: Axis, y: Axis, updateValues: UpdateValues, setChanged) => (
  rowIndex: number,
  columnIndex: number
): Node => {
  return (
    <EditableCell
      onConfirm={(val, rowIdx, colIdx) =>
        handleConfirm(val, values, x, y, rowIdx, colIdx, updateValues, setChanged)
      }
      rowIndex={rowIndex}
      columnIndex={columnIndex}
      value={returnData(values, ...idsFromIndex(x, y, rowIndex, columnIndex))}
    />
  );
};

// Renders the row headers based on axis names
const renderRowHeader = (x: Axis) => (rowIndex: number): Node => {
  return <RowHeaderCell>{x[rowIndex].name}</RowHeaderCell>;
};

// Renders the columns (and also cells) based on props
const renderColumns = (
  values: Values,
  x: Axis,
  y: Axis,
  updateValues: UpdateValues,
  typeKey: string,
  setChanged
) => {
  return y.map(col => (
    <Column
      key={`MATRIX_${typeKey}_${col.id}`}
      name={col.name}
      cellRenderer={renderCell(values, x, y, updateValues, setChanged)}
    />
  ));
};

// Renders the matrix componenet
const Matrix = ({ x, y, handleSubmit, typeKey, values }: MatrixProps) => {
  const [inputValues, setInputValues] = useState(values);
  const [changed, setChanged] = useState(false);
  return (
    <div>
      <Table numRows={x.length} rowHeaderCellRenderer={renderRowHeader(x)}>
        {renderColumns(inputValues, x, y, setInputValues, typeKey, setChanged)}
      </Table>
      <Button
        style={{ position: 'fixed', bottom: '30px', right: '50px' }}
        type="submit"
        text="Save Changes"
        disabled={!changed}
        onClick={() => {
          handleSubmit(typeKey, inputValues);
          setChanged(false);
        }}
        intent={Intent.SUCCESS}
        large
      />
    </div>
  );
};

export default Matrix;
