// @flow
import React from 'react';
import { connect } from 'react-redux';

import { getSelectedScenarioId, getCertaintyDisplay } from './selectors';

import Header from './Header';
import ScenarioForm from './ScenarioForm';
import BenefitSensitivity from './BenefitSensitivity';
import DeliverableSensitivity from './DeliverableSensitivity';
import PhaseSensitivity from './PhaseSensitivity';
import BenefitBuildupSensitivity from './BenefitBuildupSensitivity';
import PhaseCostSensitivity from './PhaseCostSensitivity';

import type { InterfaceProps } from './types';
import type { State } from '../../reducers/types';

const renderInput = (display: ?string, scenarioId: ?string) => {
  switch (display) {
    case 'root':
      if (typeof scenarioId !== 'string') {
        throw new Error('Received non-string scenario id when rendering certainty interface');
      }
      return (
        <div style={{ position: 'relative', top: '85px', padding: '10px' }}>
          <ScenarioForm scenarioId={scenarioId} />
        </div>
      );
    case 'benefitBuildupSensitivity':
      if (typeof scenarioId !== 'string') {
        throw new Error('Received non-string scenario id when rendering certainty interface');
      }
      return (
        <div style={{ position: 'relative', top: '85px', padding: '10px' }}>
          <BenefitBuildupSensitivity scenarioId={scenarioId} />
        </div>
      );
    case 'phaseCostSensitivity':
      if (typeof scenarioId !== 'string') {
        throw new Error('Received non-string scenario id when rendering certainty interface');
      }
      return (
        <div style={{ position: 'relative', top: '85px', padding: '10px' }}>
          <PhaseCostSensitivity scenarioId={scenarioId} />
        </div>
      );
    case 'benefitSensitivity':
      if (typeof scenarioId !== 'string') {
        throw new Error('Received non-string scenario id when rendering certainty interface');
      }
      return (
        <div style={{ position: 'relative', top: '85px', padding: '10px' }}>
          <BenefitSensitivity scenarioId={scenarioId} width="350px" />
        </div>
      );
    case 'deliverableSensitivity':
      if (typeof scenarioId !== 'string') {
        throw new Error('Received non-string scenario id when rendering certainty interface');
      }
      return (
        <div style={{ position: 'relative', top: '85px', padding: '10px' }}>
          <DeliverableSensitivity scenarioId={scenarioId} width="250px" />
        </div>
      );
    case 'phaseSensitivity':
      if (typeof scenarioId !== 'string') {
        throw new Error('Received non-string scenario id when rendering certainty interface');
      }
      return (
        <div style={{ position: 'relative', top: '85px', padding: '10px' }}>
          <PhaseSensitivity scenarioId={scenarioId} width="350px" />
        </div>
      );
    default:
      return <div />;
  }
};

const CertaintyEditor = ({ dark, scenarioId, display }: InterfaceProps) => (
  <div>
    <Header dark={dark} />
    {renderInput(display, scenarioId)}
  </div>
);

const mapStateToProps = (state: State) => ({
  dark: state.editor.dark,
  scenarioId: getSelectedScenarioId(state),
  display: getCertaintyDisplay(state),
});

export default connect(
  mapStateToProps,
  null
)(CertaintyEditor);
