// @flow
import React from 'react';
import { connect } from 'react-redux';

// Selectors
import { getOptions } from './selectors';

// Autocomplete component
import AutoInput from 'react-autocomplete-input';

// Overrride style sheet
import './custom.css';

// Flow Types
import { type EquationInputProps } from './types';

export const EquationInput = ({ options, initialValue, ...props }: EquationInputProps) => {
  return (
    <AutoInput
      style={{ width: '100%' }}
      Component={'input'}
      options={options}
      trigger={''}
      spacer={' '}
      matchAny={true}
      {...props}
    />
  );
};

const mapStateToProps = state => ({
  options: getOptions(state),
});

export default connect(
  mapStateToProps,
  () => ({})
)(EquationInput);
