// @flow
import React from 'react';
import { connect } from 'react-redux';

import moment from 'moment';

import { updateSensitivity } from '../../actions/model';

import * as API from '../../model/api';

import { H5, NumericInput, FormGroup, Card } from '@blueprintjs/core';
import { DateInput } from '@blueprintjs/datetime';

import { SubmitButton } from '../../components/Form/Form';
import { SwitchInput } from './BenefitSensitivity';

import { type Phase } from '../../model/api/types';

import { type State } from '../../reducers/types';
import { type Dispatch } from '../../actions/types';
import { PHASE_SLIP_RULE_FIXED, DATE_FORMAT } from '../../config/constants';

type CurrentValues = Array<{
  id: string,
  switch: boolean,
  duration: string | number,
  start: string,
}>;

type PhaseSensitivityProps = {
  scenarioId: string,
  phases: Array<Phase>,
  phaseSensitivity: CurrentValues,
  handleSave: (values: CurrentValues) => void,
  width: string | number,
};

type DurationInputProps = {
  label: string,
  labelInfo?: string,
  helperText?: string,
  handleChange: (value: number | string) => void,
  value: number | string,
};

type StartDateInputProps = {
  label: string,
  labelInfo?: string,
  helperText?: string,
  handleChange: (value: string) => void,
  value: string,
};

type PhaseSensitivityItemProps = {
  phase: Phase,
  switchState: boolean,
  duration: string | number,
  start: string,
  handleSwitchChange: () => void,
  handleDurationChange: (value: number | string) => void,
  handleStartChange: (value: string) => void,
  width: string | number,
};

const DurationInput = ({
  label,
  labelInfo,
  helperText,
  handleChange,
  value,
}: DurationInputProps) => {
  // Create custom handle for blueprint component
  const handleValueChange = (num, str) => {
    if (Number.isNaN(num) || str[str.length - 1] === '.') {
      handleChange(str);
    } else {
      handleChange(num);
    }
  };

  return (
    <FormGroup label={label} labelInfo={labelInfo} helperText={helperText}>
      <NumericInput
        allowNumericCharactersOnly={false}
        clampValueOnBlur
        min={0}
        onValueChange={handleValueChange}
        value={value}
      />
    </FormGroup>
  );
};

const StartDateInput = ({
  label,
  labelInfo,
  helperText,
  value,
  handleChange,
}: StartDateInputProps) => {
  // Parse date as JS date object
  const dateValue = moment(value, DATE_FORMAT, true).toDate();

  // Create custom handle for blueprint date component
  const handleDateChange = (selectedDate: Date, userChange: boolean) => {
    if (userChange) {
      handleChange(moment(selectedDate).format(DATE_FORMAT));
    }
  };
  return (
    <FormGroup label={label} labelInfo={labelInfo} helperText={helperText}>
      <DateInput
        value={dateValue}
        onChange={handleDateChange}
        parseDate={dateStr => moment(dateStr, DATE_FORMAT, true).toDate()}
        formatDate={dateObj => moment(dateObj).format(DATE_FORMAT)}
        maxDate={moment('2040-12-31', DATE_FORMAT, true).toDate()}
        canClearSelection={false}
      />
    </FormGroup>
  );
};

const handleSwitchChange = (
  targetId: string,
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void
) => () => {
  const filteredValues = currentValues.filter(sensitivity => sensitivity.id !== targetId);
  const foundValue = currentValues.find(sensitivity => sensitivity.id === targetId);

  if (!foundValue) {
    throw new Error(`Could not find sensitivity value for phase id ${targetId}`);
  }

  const newValues = [...filteredValues, { ...foundValue, switch: !foundValue.switch }];
  setCurrentValues(newValues);
};

const handleDurationChange = (
  targetId: string,
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void
) => (value: string | number) => {
  const filteredValues = currentValues.filter(sensitivity => sensitivity.id !== targetId);
  const foundValue = currentValues.find(sensitivity => sensitivity.id === targetId);
  const newValues = [...filteredValues, { ...foundValue, duration: value }];
  setCurrentValues(newValues);
};

const handleStartDateChange = (
  targetId: string,
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void
) => (value: string) => {
  const filteredValues = currentValues.filter(sensitivity => sensitivity.id !== targetId);
  const foundValue = currentValues.find(sensitivity => sensitivity.id === targetId);
  const newValues = [...filteredValues, { ...foundValue, start: value }];
  setCurrentValues(newValues);
};

const PhaseSensitivityItem = ({
  phase,
  switchState,
  duration,
  start,
  handleSwitchChange,
  handleDurationChange,
  handleStartChange,
  width,
}: PhaseSensitivityItemProps) => {
  const title = `${phase.name} (${
    phase.slip === PHASE_SLIP_RULE_FIXED ? 'Fixed Cost' : 'Time and Materials'
  })`;
  return (
    <Card style={{ marginBottom: '10px', marginRight: '10px', width }}>
      <H5>{title}</H5>
      <SwitchInput
        label="Switch"
        labelInfo="(required)"
        handleChange={handleSwitchChange}
        value={switchState}
      />
      <DurationInput
        label="Duration"
        labelInfo="(required)"
        helperText="Duration of the phase (for this scenario) in weeks"
        handleChange={handleDurationChange}
        value={duration}
      />
      <StartDateInput
        label="Start Date"
        labelInfo="(required)"
        helperText="Start date of the phase"
        handleChange={handleStartChange}
        value={start}
      />
    </Card>
  );
};

const renderPhaseSensitivityItem = (
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void,
  width: string | number
) => phase => {
  const foundSensitivity = currentValues.find(sensitivity => sensitivity.id === phase.id);
  if (!foundSensitivity) {
    throw new Error(`Could not find phase sensitivity with id ${phase.id}`);
  }

  const { switch: switchState, duration, start } = foundSensitivity;

  const handleSwitchCallback = handleSwitchChange(phase.id, currentValues, setCurrentValues);
  const handleDurationCallback = handleDurationChange(phase.id, currentValues, setCurrentValues);
  const handleStartCallback = handleStartDateChange(phase.id, currentValues, setCurrentValues);

  return (
    <PhaseSensitivityItem
      key={`PHASE_SENSITIVITY_ITEM_${phase.id}`}
      phase={phase}
      switchState={switchState}
      duration={duration}
      start={start}
      handleSwitchChange={handleSwitchCallback}
      handleDurationChange={handleDurationCallback}
      handleStartChange={handleStartCallback}
      width={width}
    />
  );
};

const PhaseSensitivity = ({
  phases,
  phaseSensitivity,
  handleSave,
  width,
}: PhaseSensitivityProps) => {
  const [currentValues, setCurrentValues] = React.useState(phaseSensitivity);

  return (
    <div style={{ display: 'flex', flexWrap: 'wrap' }}>
      {phases.map(renderPhaseSensitivityItem(currentValues, setCurrentValues, width))}
      <SubmitButton onClick={() => handleSave(currentValues)} />
    </div>
  );
};

const mapStateToProps = (state: State, ownProps: { scenarioId: string }) => ({
  phases: API.getPhases(state.model),
  phaseSensitivity: API.getScenarioById(state.model, ownProps.scenarioId).phaseSensitivity,
});

const mapDispatchToProps = (dispatch: Dispatch, ownProps: { scenarioId: string }) => ({
  handleSave: values =>
    dispatch(updateSensitivity(ownProps.scenarioId, 'phaseSensitivity', values)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PhaseSensitivity);
