// @flow
import React from 'react';
import { connect } from 'react-redux';

import { updateSensitivity } from '../../actions/model';

import * as API from '../../model/api';

import { H5, Switch, NumericInput, FormGroup, Tag, Card } from '@blueprintjs/core';

import { SubmitButton } from '../../components/Form/Form';

import { type Benefit } from '../../model/api/types';

import { type State } from '../../reducers/types';
import { type Dispatch } from '../../actions/types';
import { BENEFIT_TYPE_FINANCIAL } from '../../config/constants';
import { getBenefitSensitivityValues } from './selectors';

type CurrentValues = Array<{ id: string, switch: boolean, sensitivity: string | number }>;

type BenefitSensitivityProps = {
  scenarioId: string,
  benefits: Array<Benefit>,
  benefitSensitivity: CurrentValues,
  handleSave: (values: CurrentValues) => void,
  width: string | number,
};

type SensitivityInputProps = {
  label: string,
  labelInfo?: string,
  helperText?: string,
  handleChange: (value: number | string) => void,
  value: number | string,
};

type SwitchInputProps = {
  label: string,
  labelInfo?: string,
  handleChange: () => void,
  value: boolean,
};

type BenefitSensitivityItemProps = {
  benefit: Benefit,
  switchState: boolean,
  sensitivity: string | number,
  handleSwitchChange: () => void,
  handleSensitivityChange: (value: number | string) => void,
  width: string | number,
};

const SensitivityInput = ({
  label,
  labelInfo,
  helperText,
  handleChange,
  value,
}: SensitivityInputProps) => {
  // Create custom handle for blueprint component
  const handleValueChange = (num, str) => {
    if (Number.isNaN(num) || str[str.length - 1] === '.') {
      handleChange(str);
    } else {
      handleChange(num);
    }
  };

  return (
    <FormGroup label={label} labelInfo={labelInfo} helperText={helperText}>
      <NumericInput
        allowNumericCharactersOnly={false}
        clampValueOnBlur
        max={200}
        min={-200}
        onValueChange={handleValueChange}
        rightElement={<Tag icon="percentage" />}
        value={value}
      />
    </FormGroup>
  );
};

export const SwitchInput = ({ label, labelInfo, value, handleChange }: SwitchInputProps) => (
  <FormGroup label={label} labelInfo={labelInfo} inline>
    <Switch checked={value} onChange={handleChange} />
  </FormGroup>
);

const handleSwitchChange = (
  targetId: string,
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void
) => () => {
  const filteredValues = currentValues.filter(sensitivity => sensitivity.id !== targetId);
  const foundValue = currentValues.find(sensitivity => sensitivity.id === targetId);

  if (!foundValue) {
    throw new Error(`Could not find sensitivity value for benefit id ${targetId}`);
  }

  const newValues = [...filteredValues, { ...foundValue, switch: !foundValue.switch }];
  setCurrentValues(newValues);
};

const handleSensitivityChange = (
  targetId: string,
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void
) => (value: string | number) => {
  const filteredValues = currentValues.filter(sensitivity => sensitivity.id !== targetId);
  const foundValue = currentValues.find(sensitivity => sensitivity.id === targetId);
  const newValues = [...filteredValues, { ...foundValue, sensitivity: value }];
  setCurrentValues(newValues);
};

const BenefitSensitivityItem = ({
  benefit,
  switchState,
  sensitivity,
  handleSwitchChange,
  handleSensitivityChange,
  width,
}: BenefitSensitivityItemProps) => {
  const title = `${benefit.name} (${
    benefit.type === BENEFIT_TYPE_FINANCIAL ? 'Financial' : 'Non-Financial'
  })`;
  return (
    <Card style={{ marginBottom: '10px', marginRight: '10px', width }}>
      <H5>{title}</H5>
      <SwitchInput
        label="Switch"
        labelInfo="(required)"
        handleChange={handleSwitchChange}
        value={switchState}
      />
      <SensitivityInput
        label="Sensitivity"
        labelInfo="(required)"
        helperText="Percentage increase/decrease applied to benefit"
        handleChange={handleSensitivityChange}
        value={sensitivity}
      />
    </Card>
  );
};

const renderBenefitSensitivityItem = (
  currentValues: CurrentValues,
  setCurrentValues: (values: CurrentValues) => void,
  width: string | number
) => benefit => {
  const foundSensitivity = currentValues.find(sensitivity => sensitivity.id === benefit.id);
  if (!foundSensitivity) {
    throw new Error(`Could not find benefit sensitivity with id ${benefit.id}`);
  }

  const { switch: switchState, sensitivity } = foundSensitivity;

  const handleSwitchCallback = handleSwitchChange(benefit.id, currentValues, setCurrentValues);
  const handleSensitivityCallback = handleSensitivityChange(
    benefit.id,
    currentValues,
    setCurrentValues
  );

  return (
    <BenefitSensitivityItem
      key={`BENEFIT_SENSITIVITY_ITEM_${benefit.id}`}
      benefit={benefit}
      switchState={switchState}
      sensitivity={sensitivity}
      handleSwitchChange={handleSwitchCallback}
      handleSensitivityChange={handleSensitivityCallback}
      width={width}
    />
  );
};

const BenefitSensitivity = ({
  benefits,
  benefitSensitivity,
  handleSave,
  width,
}: BenefitSensitivityProps) => {
  const [currentValues, setCurrentValues] = React.useState(benefitSensitivity);

  return (
    <div style={{ display: 'flex', flexWrap: 'wrap' }}>
      {benefits.map(renderBenefitSensitivityItem(currentValues, setCurrentValues, width))}
      <SubmitButton onClick={() => handleSave(currentValues)} />
    </div>
  );
};

const mapStateToProps = (state: State, ownProps: { scenarioId: string }) => ({
  benefits: API.getBenefits(state.model),
  benefitSensitivity: getBenefitSensitivityValues(state, ownProps.scenarioId),
});

const mapDispatchToProps = (dispatch: Dispatch, ownProps: { scenarioId: string }) => ({
  handleSave: values =>
    dispatch(
      updateSensitivity(
        ownProps.scenarioId,
        'benefitSensitivity',
        values.map(value => ({
          ...value,
          sensitivity: value.sensitivity / 100,
        }))
      )
    ),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BenefitSensitivity);
