// @flow
import React from 'react';

// Blueprint Components
import { Popover, H5, Text, Button, Classes, Intent } from '@blueprintjs/core';

import type { ConfirmPopoverProps } from './types';

export const ConfirmPopover = ({
  children,
  title,
  text,
  dismissLabel,
  onClick,
  confirmLabel,
  ...props
}: ConfirmPopoverProps) => {
  return (
    <Popover {...props}>
      {children}
      <div style={{ padding: 10 }}>
        <H5>{title}</H5>
        {Array.isArray(text) ? (
          text.map((t, index) => <Text key={`confirmation_${index}`}>{t}</Text>)
        ) : (
          <Text>{text}</Text>
        )}
        <div style={{ display: 'flex', justifyContent: 'flex-end', marginTop: 15 }}>
          <Button className={Classes.POPOVER_DISMISS} style={{ marginRight: 10 }}>
            {dismissLabel}
          </Button>
          <Button intent={Intent.DANGER} className={Classes.POPOVER_DISMISS} onClick={onClick}>
            {confirmLabel}
          </Button>
        </div>
      </div>
    </Popover>
  );
};

ConfirmPopover.defaultProps = {
  confirmLabel: 'Confirm',
  dismissLabel: 'Cancel',
};

export default ConfirmPopover;
