// @flow
import { connect } from 'react-redux';

import { getMatrixValues } from './selectors';

// Model API
import * as API from '../../model/api';

// Reusable Components
import Matrix from '../../components/InputComponents/Matrix';
import { updateSensitivity } from '../../actions/model';

const mapStateToProps = (state, ownProps: { scenarioId: string }) => ({
  x: API.getBenefits(state.model),
  y: API.getPhases(state.model),
  values: getMatrixValues(state, ownProps.scenarioId, 'benefitBuildupSensitivity'),
  typeKey: 'benefitBuildupSensitivity',
});

const mapDispatchToProps = (dispatch, ownProps: { scenarioId: string }) => ({
  handleSubmit: (typeKey, values) =>
    dispatch(updateSensitivity(ownProps.scenarioId, 'benefitBuildupSensitivity', values)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Matrix);
