// @flow
import React from 'react';
import { connect } from 'react-redux';

import { SCENARIO_FORM, SCENARIO_KEY } from '../../config/constants';

import { type Dispatch } from '../../actions/types';
import { updateAtom } from '../../actions/model';
import { getInitialValues } from './selectors';

import Form from '../../components/Form';

import { type State } from '../../reducers/types';

const mapStateToProps = (state: State, ownProps: { scenarioId: string }) => ({
  formData: SCENARIO_FORM,
  initialValues: getInitialValues(state, ownProps.scenarioId),
  model: state.model,
});

const mapDispatchToProps = (dispatch: Dispatch, ownProps: { scenarioId: string }) => ({
  handleUpdate: values => dispatch(updateAtom(ownProps.scenarioId, SCENARIO_KEY, values)),
});

const ConnectedForm = connect(
  mapStateToProps,
  mapDispatchToProps
)(Form);

export default ({ scenarioId }: { scenarioId: string }) => (
  <ConnectedForm scenarioId={scenarioId} />
);
