// @flow
import * as API from '../../model/api';
import { type BenefitSensitivity } from '../../model/api/types';
import { type State } from '../../reducers/types';
import * as CONSTANTS from '../../config/constants';

type Display =
  | 'benefitSensitivity'
  | 'deliverableSensitivity'
  | 'phaseSensitivity'
  | 'phaseCostSensitivity'
  | 'benefitBuildupSensitivity'
  | 'root';

export function getInitialValues(
  state: State,
  scenarioId: string
): { id: string, name: string, definition: string } {
  const found = API.getScenarioById(state.model, scenarioId);
  return {
    id: found.id,
    name: found.name,
    definition: found.definition,
  };
}

export function getCertaintyDisplay(state: State): Display | void {
  const { selected } = state.certainty.explorer;
  if (selected) {
    switch (selected.type.id) {
      case CONSTANTS.SCENARIO_ID:
        return 'root';
      case CONSTANTS.PHASE_SENSITIVITY_ID:
        return 'phaseSensitivity';
      case CONSTANTS.BENEFIT_SENSITIVITY_ID:
        return 'benefitSensitivity';
      case CONSTANTS.DELIVERABLE_SENSITIVITY_ID:
        return 'deliverableSensitivity';
      case CONSTANTS.PHASE_COST_SENSITIVITY_ID:
        return 'phaseCostSensitivity';
      case CONSTANTS.BENEFIT_BUILDUP_SENSITIVITY_ID:
        return 'benefitBuildupSensitivity';
      default:
        throw new Error(
          'Tried to get certainty display, something was selected, but type was not recognised'
        );
    }
  }
  return undefined;
}

export function getSelectedScenarioId(state: State): ?string {
  const { selected } = state.certainty.explorer;
  if (selected) {
    return selected.scenarioId;
  } else {
    return undefined;
  }
}

export function getMatrixValues(
  state: State,
  scenarioId: string,
  matrix: 'phaseCostSensitivity' | 'benefitBuildupSensitivity'
): { [string]: { [string]: number } } {
  const scenarios = API.getScenarios(state.model);
  const foundScenario = scenarios.find(scenario => scenario.id === scenarioId);
  if (!foundScenario) {
    throw new Error(`Could not find scenario with id ${scenarioId} when rendering ${matrix}`);
  }
  return foundScenario[matrix];
}

export function getBenefitSensitivityValues(state: State, scenarioId: string) {
  const { benefitSensitivity } = API.getScenarioById(state.model, scenarioId);

  const sensitivity: BenefitSensitivity = benefitSensitivity.map(entry => {
    return {
      ...entry,
      sensitivity: entry.sensitivity * 100,
    };
  });

  return sensitivity;
}
