// @flow
import * as API from '../../model/api';
import { type State } from '../../reducers/types';

import { type Options } from './types';

export const getOptions = (state: State): Options => {
  const { model } = state;
  const { measures, parameters, stakeholders } = API.getProgramme(model);

  const measureOptions = measures.reduce((options, measure) => {
    options.push(`mb_${measure.name.replace(/ /gi, '_')}`);
    options.push(`ms_${measure.name.replace(/ /gi, '_')}`);
    options.push(`mc_${measure.name.replace(/ /gi, '_')}`);
    return options;
  }, []);
  const parameterOptions = parameters.reduce((options, parameter) => {
    options.push(parameter.name);
    return options;
  }, measureOptions);
  const stakeholderOptions = stakeholders.reduce((options, stakeholder) => {
    options.push(`sr_${stakeholder.name.replace(/ /gi, '_')}`);
    options.push(`sm_${stakeholder.name.replace(/ /gi, '_')}`);
    return options;
  }, parameterOptions);

  return stakeholderOptions;
};
