// @flow
import React from 'react';
import type { Node } from 'react';
import { connect } from 'react-redux';
import * as selectors from './selectors';

import { Breadcrumbs } from '@blueprintjs/core';

import type { Breadcrumbs as BreadcrumbsType } from '../types';

import type { State } from '../../../reducers/types';

export type CertaintyBreadcrumbsProps = {
  breadcrumbs: Array<BreadcrumbsType>,
};

const CertaintyBreadcrumbs = ({ breadcrumbs }: CertaintyBreadcrumbsProps): Node => (
  <div style={{ width: '100%' }}>
    <Breadcrumbs items={breadcrumbs} />
  </div>
);

const mapStateToProps = (state: State) => ({
  breadcrumbs: selectors.getBreadcrumbsData(state),
});

export default connect(
  mapStateToProps,
  null
)(CertaintyBreadcrumbs);
