// @flow
import React from 'react';
import type { Node } from 'react';

// Connect function
import { connect } from 'react-redux';

// Blueprint Components
import { Button, ButtonGroup, Intent } from '@blueprintjs/core';

// Reusable Components
import ConfirmPopover from '../../../components/ConfirmPopover';

// Selectors
import { getCertaintyDisplay } from '../selectors';

// Styles
import { toolbarContainer } from './styles';

// Flow Types
import type { ToolbarProps } from '../types';

const ResetButton = ({ display, handleReset }) => {
  let title, text, button;
  switch (display) {
    case 'phaseSensitivity':
      title = 'Reset Phase Values to Model Values';
      text = 'This will reset all the phase values to those defined in the main model.';
      button = 'Reset Phases to Model Values';
      break;
    case 'benefitBuildupSensitivity':
    case 'phaseCostSensitivity':
      title = 'Reset Sensitivity Values to Zero';
      text = 'This will reset all the sensitivity values in the matrix to zero.';
      button = 'Reset to Zero';
      break;
    case 'benefitSensitivity':
      title = 'Reset Sensitivity Values to Zero';
      text = 'This will reset all benefit sensitivity values to zero.';
      button = 'Reset to Zero';
      break;
    case 'root':
      title = 'Reset ALL Scenario Values to Model Values';
      text = 'This will reset the whole scenario back to that defined in the model';
      button = 'Reset Scenario';
      break;
    default:
      throw new Error('Tried to render a reset button on a non-resetable scenario type');
  }
  return (
    <ButtonGroup style={{ float: 'right' }}>
      <ConfirmPopover title={title} text={text} onClick={handleReset}>
        <Button small intent={Intent.DANGER}>
          {button}
        </Button>
      </ConfirmPopover>
    </ButtonGroup>
  );
};

const Toolbar = ({ handleReset, display }: ToolbarProps): Node => (
  <div style={toolbarContainer}>
    {display === 'root' && (
      <ButtonGroup>
        <Button small icon="duplicate">
          Duplicate Scenario
        </Button>
        <ConfirmPopover
          title="Delete Scenario?"
          text="This will remove the entire scenario from the model?"
        >
          <Button small icon="trash" intent={Intent.DANGER}>
            Delete Scenario
          </Button>
        </ConfirmPopover>
      </ButtonGroup>
    )}
    {['benefitSensitivity', 'deliverableSensitivity', 'phaseSensitivity'].includes(display) && (
      <ButtonGroup>
        <Button small>Select All</Button>
        <Button small>Deselect All</Button>
      </ButtonGroup>
    )}
    {[
      'phaseSensitivity',
      'phaseCostSensitivity',
      'benefitBuildupSensitivity',
      'benefitSensitivity',
      'root',
    ].includes(display) && <ResetButton display={display} handleReset={handleReset} />}
  </div>
);

const mapStateToProps = state => ({
  display: getCertaintyDisplay(state),
});

const mapDispatchToProps = state => ({
  handleReset: () => console.log('reset values'),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Toolbar);
