// @flow
import { connect } from 'react-redux';

import { getMatrixValues } from './selectors';

// Model API
import * as API from '../../model/api';

// Reusable Components
import Matrix from '../../components/InputComponents/Matrix';
import { updateSensitivity } from '../../actions/model';

const mapStateToProps = (state, ownProps: { scenarioId: string }) => ({
  x: [{ id: 'DEVELOPMENT_COST', name: 'Development' }, { id: 'SUPPORT_COST', name: 'Support' }],
  y: API.getPhases(state.model),
  values: getMatrixValues(state, ownProps.scenarioId, 'phaseCostSensitivity'),
  typeKey: 'phaseCostSensitivity',
});

const mapDispatchToProps = (dispatch, ownProps: { scenarioId: string }) => ({
  handleSubmit: (typeKey, values) =>
    dispatch(updateSensitivity(ownProps.scenarioId, 'phaseCostSensitivity', values)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Matrix);
